








































































































import axios from 'axios'
import {Component, Mixins} from "vue-property-decorator";
import att_mixins from "@/mixins/att";
import expandRow from "@/components/expandTable.vue";

@Component({components: {}})
export default class my_order extends Mixins(att_mixins) {
    reject = false;
    openModal = false;
    columnsName = [
        {
            title: '工单编号:',
            key: 'WorkId',
            sortable: true
        },
        {
            title: '工单说明',
            key: 'Text',
            tooltip: true
        },
        {
            title: '是否备份',
            key: 'Backup'
        },
        {
            title: '提交时间:',
            key: 'Date',
            sortable: true
        },
        {
            title: '审核/执行人',
            key: 'Assigned',
            sortable: true
        },
        {
            title: '多级审核执行人',
            key: 'Executor',
            sortable: true
        },
        {
            title: '状态',
            key: 'Status',
            render: (h: any, params: { row: { Status: number }; }) => {
                const row = params.row;
                let color = '';
                let text = '';
                if (row.Status === 2) {
                    color = 'primary';
                    text = '待审核';
                } else if (row.Status === 0) {
                    color = 'error';
                    text = '驳回';
                } else if (row.Status === 1) {
                    color = 'success';
                    text = '已执行';
                } else if (row.Status === 4) {
                    color = 'error';
                    text = '执行失败';
                } else if (row.Status === 5) {
                    color = 'primary';
                    text = '待执行';
                } else {
                    color = 'warning';
                    text = '执行中';
                }

                return h('Tag', {
                    props: {
                        type: 'dot',
                        color: color
                    }
                }, text)
            },
            sortable: true
        },
        {
            title: '操作',
            key: 'action',
            align: 'center',
            slot: 'action',
            width: 300
        }
    ];
    reject_text = '';
    table_data = [] as any;
    multi = false;
    sql = [];
    sql_columns = [
        {
            type: 'expand',
            width: 50,
            render: (h: any, params: { row: { SQL: string } }) => {
                return h(expandRow, {
                    props: {
                        row: params.row.SQL
                    }
                })
            }
        },
        {
            title: 'sql',
            key: 'SQL'
        }
    ];

    open_modal(vl: any) {
        axios.get(`${this.$config.url}/fetch/sql?k=${vl.WorkId}`)
            .then(res => {
                this.sql = res.data
            })
            .catch(err => this.$config.err_notice(this, err));
        this.openModal = true;
        this.formItem = vl
    }

    currentpage(vl = 1) {
        axios.put(`${this.$config.url}/user/order`, {
            page: vl,
            find: this.find
        })
            .then(res => {
                if (!res.data.multi) {
                    for (let i = 0; i < this.columnsName.length; i++) {
                        if (this.columnsName[i].key === 'Executor') {
                            this.columnsName.splice(i, 1)
                        }
                    }
                }
                this.table_data = res.data.data;
                this.table_data.forEach((item: { Backup: string | number; }) => {
                    (item.Backup === 1) ? item.Backup = '是' : item.Backup = '否'
                });
                this.page_number = parseInt(res.data.page)
            })
            .catch(error => {
                this.$config.err_notice(this, error)
            })
    }

    queryData() {
        this.find.valve = true;
        this.currentpage()
    }

    queryCancel() {
        this.find = this.$config.clearPicker(this.find);
        this.current = 1;
        this.currentpage()
    }

    openOrder(row: { WorkId: string, Status: string }) {
        this.$router.push({
            name: 'order_detail',
            query: {
                workid: row.WorkId,
                status: row.Status.toString()
            }
        })
    }

    orderReject(row: { Rejected: string }) {
        this.reject = true;
        this.reject_text = row.Rejected;
    }

    delOrder(row: { WorkId: string }) {
        axios.get(`${this.$config.url}/fetch/undo?work_id=${row.WorkId}`)
            .then(res => {
                this.$config.notice(res.data);
                this.currentpage(this.current)
            })
            .catch(error => {
                this.$config.err_notice(this, error)
            })
    }

    mounted() {
        this.currentpage()
    }
}
